* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app {
  background: linear-gradient(
    106.5deg,
    rgba(255, 215, 185, 0.91) 23%,
    rgba(223, 159, 247, 0.8) 93%
  );
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login {
  width: 50%;
  background-color: white;
  height: 50vh;
  display: flex;
  justify-content: center;
}
.loginContainer {
  margin-top: 25px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.form {
  margin-top: 30px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.form input {
  margin-top: 20px;
  width: 300px;
  height: 30px;
  outline: none;
  border-radius: 4px;
  border: 1px solid gray;
  padding: 10px;
}
.form > p {
  margin-top: 20px;
}
.text {
  margin-top: 20px;
}
.para {
  all: unset;
  display: inline-block;
  cursor: pointer;
}
.para:hover {
  color: aliceblue;
  font-weight: bolder;
  width: 90px;
  /* border: 1px solid; */
  height: 25px;
  padding: 10px;
  text-align: center;
  background: linear-gradient(
    106.5deg,
    rgba(230, 184, 148, 0.91) 23%,
    rgba(193, 122, 218, 0.8) 93%
  );
  border-radius: 30px;
}
.btn {
  background: linear-gradient(
    106.5deg,
    rgba(255, 215, 185, 0.91) 23%,
    rgba(223, 159, 247, 0.8) 93%
  );
  font-weight: bolder;
  color: purple;
  outline: navajowhite;
}
.btn:hover {
  background: linear-gradient(
    106.5deg,
    rgba(230, 184, 148, 0.91) 23%,
    rgba(193, 122, 218, 0.8) 93%
  );
  box-shadow: 0px 0px 10px gray;
}

.back{
  all: unset;
  color: purple;
  font-weight: bolder;
  cursor: pointer;
  padding: 30px;
 
}

@media screen and (max-width: 700px) {
  .form input {
    width: auto;
  }

  .text {
    display: flex;
    flex-direction: column;
  }
  .login {
    height: 60vh;
    width: 55%;
  }
}

